import React, { useState } from 'react'
import { Redirect } from 'react-router-dom'
import { useSelector } from 'react-redux'
import SelectedSKUList from 'Page/StockEntry/StockTransfer/WeightEntry/SelectedSKUList'
import WeightEntryList from 'Page/StockEntry/StockTransfer/WeightEntry/WeightEntryList'

const WeightEntry = () => {
    const stockTransferEntryData = useSelector(state => state.stockTransferEntryData)
    const [hideSKUList, setHideSKUList] = useState(false)
    const [activeSku, setActiveSKU] = useState(0)
    let height = document.documentElement.clientHeight
    let clientWidth = document.documentElement.clientWidth
    let heightAdj = height - 150
    const moduleName = 'stockTransfer'
    if (!stockTransferEntryData || stockTransferEntryData?.jobPost.list.length === 0) {
        return (
            <div className='d-flex align-items-center justify-content-center text-center w-100 h-screen'><Redirect to='/inventory-entry/stock-transfer'>Please Select At Least 1 SKU</Redirect></div >)
    }
    return (
        <div style={{height: clientWidth <= 500 ? height : heightAdj }} className='w-100 d-md-flex justify-content-between scroll-y z-0'>
            <SelectedSKUList
                hideSKUList={hideSKUList}
                activeSku={activeSku}
                setActiveSKU={setActiveSKU}
                moduleName={moduleName}
                moduleEntry={stockTransferEntryData}
            />
            <WeightEntryList
                setHideSKUList={setHideSKUList}
                hideSKUList={hideSKUList}
                moduleName={moduleName}
                moduleEntry={stockTransferEntryData}
                activeSku={activeSku}
            />
        </div>
    )
}

export default WeightEntry